*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

div >h1{
    font-family: monospace;
    /* font-size: 45px; */
    
}

.hero-banner-img {
    position: absolute;
    right: 60px;
    top: 0px;
    width: 700px;
    /* height: 700px; */
    margin-top: -330px;
  
  }

  @media (min-width:300px) and (max-width: 1000px){
  .hero-banner-img{
    display: none;
  }
  }
  .Piyush{
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 15px;
  }

  .search-btn:hover{
    color: #ff2625;
    border:1px solid #ff2625;
  }
  .bodyPart-card{
  border-top:4px solid #ff2625;
       background-color:"#fff";
        border-bottom-left-radius:20px;
        width:270px;
        height:280px;
        cursor:"pointer";
        margin-bottom: 30px;
       
  }
  .bodyPart-card:hover{
    transform: scale(1.1);
    transition: .5s;
  }
  .exercise-card{
  
    border-top:4px solid #ff2625;
    text-decoration: none;

  }
  .exercise-card:hover{
    transform: scale(1.1);
    transition: .3s;
  }
  .btn:hover{
    background-color: #ff2625 !important;
    color: #fff !important;

  }
